import { ProductProvider } from "../../contexts";
import {ProductCategory, PlaceOrderSection, ProductListContainer} from "../../components";


export default function Product() {
  return (
    <ProductProvider>
      <div className="row">
        <div className="col-12 col-lg-10">
          <ProductCategory />
          <ProductListContainer />
        </div>
        <div className="col-12 col-lg-2">
          <PlaceOrderSection />
        </div>
      </div>
    </ProductProvider>
  );
}
