import React from 'react';
import {Navbar} from './layout'; 
import { Outlet } from 'react-router-dom';
import Footer from './footer'


function MainLayout() {
    return (
        <>
            <Navbar />

                {/* <ProductProvider> */}
                <div className="container-md">
                    <div className='row'>
                        <Outlet /> {/*  Outlet  */}
                    </div>
                    <hr></hr>
                    {/* footer */}
                    <div className='row'>
                        <Footer />
                    </div>
                </div>
                {/* </ProductProvider> */}

        </>
    );
}

export default MainLayout;
