// src/components/LoginForm.jsx
import React, { useState } from "react";

export default function LoginForm({ onSubmit }) {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit(email, password);
  };

  return (
    <form onSubmit={handleSubmit}>
      <h3>Please Sign in</h3>
      <div className="input-floating-label form-floating mb-4">
        <input type="email" className="form-control" value={email} onChange={(e) => setEmail(e.target.value)} placeholder="name@example.com" required />
        <label>Email address</label>
      </div>

      <div className="input-floating-label form-floating mb-4 position-relative">
        <input type="password" className="form-control" value={password} onChange={(e) => setPassword(e.target.value)} placeholder="Enter your password" required />
        <label>Password</label>
      </div>

      <div className="d-grid">
        <button className="btn btn-dark mb-0" type="submit">Login</button>
      </div>
    </form>
  );
}
