import React from "react";
import { Carousel, Container } from "react-bootstrap";
import { Box } from "@mui/material";
import Introduction from "./mainpage/introduction";
import "bootstrap/dist/css/bootstrap.min.css"; //  Bootstrap CSS 

export default function Home() {
  return (
    <Box>
      {/*  */}
      <Container fluid className="mt-3"> 
        <Carousel fade interval={3000} controls={false} indicators={true}>
          <Carousel.Item>
            <img
              src={`${process.env.PUBLIC_URL}/image/1.jpg`}
              className="d-block w-100"
              alt="Slide 1"
              style={{ height: "300px", objectFit: "cover", borderRadius: "10px" }}
            />
          </Carousel.Item>
          <Carousel.Item>
            <img
              src={`${process.env.PUBLIC_URL}/image/2.jpg`}
              className="d-block w-100"
              alt="Slide 2"
              style={{ height: "300px", objectFit: "cover", borderRadius: "10px" }}
            />
          </Carousel.Item>
          <Carousel.Item>
            <img
              src={`${process.env.PUBLIC_URL}/image/3.jpg`}
              className="d-block w-100"
              alt="Slide 3"
              style={{ height: "300px", objectFit: "cover", borderRadius: "10px" }}
            />
          </Carousel.Item>
        </Carousel>
      </Container>

      {/*  */}
      <Introduction />
    </Box>
  );
}
