import "./App.css";
import { MainLayout } from "./components";
import { Signup } from "./features";
import { Home, Login, Product } from "./pages";
import { UserProvider, ProductProvider } from "./contexts";
import { createTheme, ThemeProvider, CssBaseline } from "@mui/material"; //   ThemeProvider
import { useSelector } from "react-redux";
import { HashRouter as Router, Route, Routes } from "react-router-dom";

function App() {
  const { mode, primaryColor } = useSelector((state) => state.theme);

  //   Redux 
  const theme = createTheme({
    palette: {
      mode, // light  dark 
      primary: {
        main: primaryColor, // Redux 
      },
    },
  });

  return (
    <ThemeProvider theme={theme}> {/*   MUI  */}
      <CssBaseline /> {/*   */}
      <UserProvider>
        <ProductProvider>
          <Router>
            <Routes>
              <Route path="/" element={<MainLayout />}>
                <Route index element={<Home />} />
                <Route path="product" element={<Product />} />
                <Route path="login" element={<Login />} />
                <Route path="signup" element={<Signup />} />
              </Route>
            </Routes>
          </Router>
        </ProductProvider>
      </UserProvider>
    </ThemeProvider>
  );
}

export default App;
